<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">修改采购详情</h1>

          <label for="">平台单号</label><el-input v-model="task.supplier_ref" placeholder="请输入平台单号"></el-input>
          <label for="">采购价格</label><el-input v-model="task.price" placeholder="请输入采购价格"></el-input>
          <label for="">采购数量</label><el-input v-model="task.count" placeholder="请输入采购数量"></el-input>
          <label for="">状态</label>
          <el-select v-model="task.status" placeholder="请选择">
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <label for="">备注</label><el-input v-model="task.memo" placeholder="备注"></el-input> 
           
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return {  
            task: {
              supplier_ref: "",
              price: "",
              count: "",
              status: "",
              memo: "",
            },
           
            statusOptions: [
              {
                label: '待采购',
                value: '0'
              },
              {
                label: '已采购',
                value: '1'
              },
              {
                label: '缺货',
                value: '2'
              },
              {
                label: '取消单',
                value: '3'
              },
              // {
              //   label: '待付款',
              //   value: '4'
              // },
              // {
              //   label: '已付款',
              //   value: '5'
              // },
              // {
              //   label: '付款失败',
              //   value: '6'
              // },
              // {
              //   label: '收货中',
              //   value: '7'
              // },
              // {
              //   label: '退款中',
              //   value: '8'
              // },
              // {
              //   label: '已完成',
              //   value: '9'
              // }
            ]

          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/purchases-detail";
          let params = {};
          let config = {};
          params.supplier_ref = this.task.supplier_ref;
          params.price = this.task.price;
          params.count = this.task.count;
          params.status = this.task.status;
          params.memo = this.task.memo;
           console.log('id:'+this.task.id)
          let that = this; 
          if(that.task.id){
             config = {
              headers : { 
              //  'Content-Type':'application/x-www-form-urlencoded'
              },
            };
             url = "http://api.orkowms.me/purchases-detail/"+this.task.id ;
          } 

          this.$axios.put(url,params,config).then((res) => {
              that.$message({
                type: 'success',
                message: '修改成功!'
              });

          }).catch((err) => {
              that.$message({
                type: 'error',
                message: '修改失败!'
              });
          })
        },
         
      },
      created() {
         let row = this.$route.query;  
          this.task.id = row.id; 
          this.task.supplier_ref = row.supplier_ref;
          this.task.price = row.price;
          this.task.count = row.count;
          this.task.status = row.status;
          this.task.memo = row.memo;
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



